import { message } from 'antd';
import PubSub from 'pubsub-js'
import { isRepeat } from 'utils'

import { VerifyTagName, VerifyAddress, VerifyMax, VerifyMin } from 'api/variable'

//
export function addressVerify(value, obj) {
  return new Promise(async function (resolve, reject) {
    if (value === "") {
      message.error("")
      PubSub.publish("canSubmit", {
        canSubmit: false,
        message: ""
      })
      resolve(false)
    } else {
       await VerifyAddress(obj).then(res => {
        console.log(res)
        if (res.code !== 0) {
          message.error(res.msg)
          PubSub.publish("canSubmit", {
            canSubmit: false,
            message: res.msg
          })
          resolve(false)
        } else {
          PubSub.publish("canSubmit", {
            canSubmit: true,
            message: ""
          })
          resolve(true)
        }
      })
    }
  })
}

//
export function descVerify(value) {
  console.log("")
  //(/^[\u4E00-\u9FA5A-Za-z0-9_.%&',;=?$\x22]+$/).test(value) && 
  if (value.length > 48) {
    message.error("4824")
    
    PubSub.publish("canSubmit", {
      canSubmit: false,
      message: "4824"
    })
  }
}

//
export function nameVerify(value, dataSource, record, dataIndex, activeNodeType) {
  console.log("")
  if (!isRepeat(dataSource, record.key, dataIndex, value)) {
    if (value === "") {
      message.error("");
      PubSub.publish("canSubmit", {
        canSubmit: false,
        message: ""
      })
    } else if (!(/^[a-zA-Z_]([a-zA-Z0-9_.]+)?$/).test(value)) {
      message.error("")
      PubSub.publish("canSubmit", {
        canSubmit: false,
        message: ""
      })
    } else {
      VerifyTagName({
        tagId: record.id,
        tagName: value,
        type: activeNodeType
      }).then(res => {
        if (res.code !== 0) {
          message.error(res.msg)
          PubSub.publish("canSubmit", {
            canSubmit: false,
            message: res.msg
          })
        } else {
          PubSub.publish("canSubmit", {
            canSubmit: true,
            message: ""
          })
        }
      })
    }
  } else {
      message.error("" + value + "")
  }
}

//
export function verifyMax(value, dataType) {
  VerifyMax({
    value,
    dataType
  }).then(res => {
    if (res.code !== 0) {
      message.error(res.msg)
      PubSub.publish("canSubmit", {
        canSubmit: false,
        message: res.msg
      })
    } else {
      PubSub.publish("canSubmit", {
        canSubmit: true,
        message: ""
      })
    }
  })
}

